.App {
  padding-top: 25px;
  width: 500px;
  margin: 0 auto;
}

.header * {
  margin: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.author {
  text-decoration: none;
  font-size: 0.8rem;
  padding-top: 10px;
  color: #666;
}

.select-wrapper {
  margin-top: 15px;
  width: 500px;
  max-width: 90vw;
  text-align: left;
}

.code {
  background-color: #efefef;

  width: auto;
  margin-top: 20px;
  padding: 10px;

  line-height: 2;
  white-space: pre-wrap;

  font-family: monospace;
  font-weight: 100;
  font-size: 1.2rem;
}
